import styled from "styled-components"

const ChipWrapper = styled.div`
  padding: 0 .2rem;
  background-color: #fff;
  color: purple;
  cursor: crosshair;
`

const Chip = ( { onClick, children } ) => {
  return (
    <ChipWrapper onClick={onClick}>
      { children }
    </ChipWrapper>
  )
}

export default Chip
