import React, { useEffect, useState } from 'react'
import loginService from '../services/LoginService'
import versionResolver from '../utils/versionResolver'

const Settings = () => {
  const [username, setUsername] = useState('Loading...')
  const [realname, setRealname] = useState('')
  const [tier, setTier] = useState('')
  const [email, setEmail] = useState('')

  useEffect(() => {
    loginService.getUser().then(user => {
      setUsername(user.username)
      setRealname(user.realname)
      setTier(user.tier)
      setEmail(user.email)
    })
  })

  return (
    <div>
      <div className='container'>
        <div>
          {`version ${versionResolver}`}
        </div>
        <div>
          <div s={12} m={5}>
            <div className='red accent-2 black-text'>
              <span>Currently can't change your information</span>
            </div>
          </div>
          <div s={12} m={7}>
            <div className='blue lighten-3 black-text'>
              <div>
                <div>Username: </div>
                <div>{username}</div>
              </div>
              <div>
                <div>Name: </div>
                <div>{realname}</div>
              </div>
              <div>
                <div>Level: </div>
                <div>{tier}</div>
              </div>
              <div>
                <div>Email: </div>
                <div>{email}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Settings
