import React, { useEffect, useState } from 'react'
import Textarea from 'react-textarea-autosize'
import { connect } from 'react-redux'
import { modifyNote } from '../../reducers/noteReducer'
import noteService from '../../services/NoteService.js'
import { notify, errormessage } from '../../reducers/notificationReducer'
import Container from '../common/Container'
import Input from '../common/Input'
import Button from '../common/Button'
import Chip from '../common/Chip'
import { TextAreaWrapper } from '../common/TextArea'
import styled from 'styled-components'

const AddTagForm = styled.form`
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
`

const Edit = ( { match, errormessage, history, modifyNote, notify }) => {
  const [state, setState] = useState({
    mounted: false,
    id: null,
    title: '',
    content: ''
  })
  const [newTags, setNewTags] = useState([])
  const [tagText, setTagText] = useState('')
  const [fetched, setfetched] = useState(false)

  const fetchNote = async () => {
    if (fetched) {
      return
    }

    const note = await noteService.getOne(match.params.id)
    setfetched(true)
    try {
      setState(() => {
        return {
          id: note.id,
          title: note.title,
          content: note.content
        }
      })
      setNewTags(note.tags.filter(tag => tag !== null))
    } catch (eception) {
      errormessage(`Couldn't find note '${match.params.id}'`, 5)
      history.push('/')
    }
  }

  useEffect(() => {
    fetchNote()
  }, [])

  const handleSubmit = async (event) => {
    event.preventDefault()
    try {
      const noteObject = {
        id: state.id,
        title: state.title,
        content: state.content,
        tags: newTags
      }
      await modifyNote(noteObject)
      await notify(`you modified '${noteObject.title}'`, 10)
      history.push('/')
    } catch (exception) {
      console.error(exception)
      errormessage('ERROR WHILE EDITING NOTE', 10)
    }
  }

  const handleTagTextChange = (event) => {
    setTagText(event.target.value)
  }

  const handleFieldChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.value })
  }

  const handleContent = (event) => {
    setState({ ...state, content: event.target.value })
  }

  const addTag = async (event) => {
    event.preventDefault()
    const maxTags = 10
    if (tagText.length === 0 || newTags.includes(tagText)) {
      return
    }

    let tagTemp = [...newTags]
    tagTemp.push(tagText)

    if (tagTemp.length < maxTags) {
      setNewTags(tagTemp)
      setTagText('')
    } else {
      notify(`Maxium number of tags is '${maxTags}'`, 10)
    }
  }

  const removeTag = (tagName) => {
    setNewTags(newTags.filter(tag => tag !== tagName))
  }

  return (
    <Container>
      <div>
        <h3>Note tags:</h3>
        <p>Click tag to remove</p>
      </div>
      <div>
        <div>
          <div>
            {newTags.map(tag =>
              <Chip key={tag} onClick={() => { removeTag(tag) }}> {tag} </Chip>
            )}
          </div>
        </div>
      </div>
      <div>
        <AddTagForm id="tagform" onSubmit={addTag}>
          <div>
            Add tag <Input name="tagText" value={tagText} onChange={handleTagTextChange} />
          </div>
          <Button className="deep orange" type="submit" form="tagform">Add tag</Button>
        </AddTagForm>
      </div>
      <h2>Edit note</h2>
      <form onSubmit={handleSubmit}>
        <div>
          Title<br />
          <Input name="title" value={state.title} onChange={handleFieldChange} />
        </div>
        <TextAreaWrapper>
          <label>
            Content
          </label>
          <Textarea className="note-edit" value={state.content} onChange={handleContent} minRows={10} />
        </TextAreaWrapper>
        <br />
        <Button waves="light" className="red accent-2" type="submit">Save</Button>
      </form>
    </Container>
  )
}

const mapDispatchToProps = {
  modifyNote,
  notify,
  errormessage
}

const ConnectedEditNote = connect(
  null,
  mapDispatchToProps
)(Edit)

export default ConnectedEditNote
