import React, { useState } from 'react'
import styled from 'styled-components'
import Register from './Register'
import { ClipLoader } from 'react-spinners'
import '../App.css'
import { resolveUrl } from '../utils/environmentResolvers'
import githubLoginImage from '../assets/github_login.png'
import { Navbar } from './common/Navigation'
import Input from './common/Input'
import Icon from './common/Icon'
import Button from './common/Button'
import Container from './common/Container'

const Suggestion = styled.div`
  margin-top: 10vh;
`

const LoginSeparator = styled.div`
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #000;
  line-height: 0.1em;
  margin: 2rem 0;

  span {
    background-color: #121212;
    padding:0 10px;
  }
`

const Login = (props) => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const [register, setRegister] = useState(false)
  const [loading, setLoading] = useState(false)

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      setLoading(true)
      await props.actionForLogin({
        username: username,
        password: password
      })
    } catch (exception) {
      setLoading(false)
      setError('Bad credentials')
      console.log(exception)
      setTimeout(() => {
        setError('')
      }, 5000)
    }
  }
  const handleRegisterRedirect = async (event) => {
    event.preventDefault()
    setRegister(!register)
  }

  if (register) {
    return (
      <div>
        <Register init={props.init} />
      </div>
    )
  }

  const backendUrl = resolveUrl()
  console.log('backend url:', backendUrl)
  const githubLoginUrl = `${backendUrl}/api/login/github`

  return (
    <div>
      <Navbar className="indigo" brand='mystash' href={process.env.PUBLIC_URL} right>
      </Navbar>
      <Container>
        <div className="mystash-flex-item">
          <a href={githubLoginUrl}>
            <img src={githubLoginImage} alt="Login with GitHub" />
          </a>
        </div>
        <LoginSeparator><span>OR</span></LoginSeparator>
        <ClipLoader loading={loading} color='blue' />
        {error !== '' ? <div className="error">{error}</div> : <div></div>}
        <form onSubmit={handleLogin}>
          <div>
            username:
            <Input
              type="text"
              name="username"
              autoComplete="off"
              value={username}
              onChange={(event) => setUsername(event.target.value)}
            ><Icon>account_circle</Icon></Input>
          </div>
          <div>
            password:
            <Input
              type="password"
              name="password"
              autoComplete="off"
              value={password}
              onChange={(event) => setPassword(event.target.value)}
            ><Icon>https</Icon></Input>
          </div>
          <Button type="submit">Login</Button>
        </form>
        <Suggestion>
          Dont have account? <a onClick={handleRegisterRedirect} href="/register">Register</a>
        </Suggestion>
      </Container>
    </div >
  )
}

export default Login
